
.modal {
    display: none;
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1000;
    padding: 1rem;
    background-color: rgba(6, 15, 49, 0.9);
    overflow: auto;
    .modal__dialog {
      margin: auto;
      width: 100%;
      max-width: 1100px;
      background-color: #fff;
      position: relative;
      display: flex;
      transform: rotateX(90deg);
      transition: transform 0.2s linear;
      &--sm {
        max-width: 900px;
      }
    }
    &.show {
      display: flex;
    }
}
