body,
button,
input,
select,
optgroup,
textarea,
p {
	color: $body-text;
	@include font("default", $size: $base-font-size);
	@include phone-only{
		@include font("default", $size: $mobile-font-size);

	}
	.is-style-orange{
		color: $orange;
		@include font("buttons", 700, 13px);
		text-transform: uppercase;	
		margin-bottom: 0;
	}
}

p:last-child {
	margin-bottom: 0;
}

blockquote {
	border-left: 4px solid $primary;
	font-size: 24px;
	font-style: italic;
	padding: 4px 0 8px 16px;
}
